.video-presentation {
  padding-top: 43px;

  @media (min-width: @tablet-width) {
    padding-top: 58px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 62px;
  }
}

.video-presentation__title {
  margin: 0 auto 29px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 30px;
  }
}

.video-presentation__info {
  margin: 0 auto 38px;
  color: #333333;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: @tablet-width) {
    margin-bottom: 66px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 64px;
  }
}

.video-presentation__player {
  position: relative;
}

.video-presentation__image {
  display: block;
  width: 100%;
  height: 177px;
  margin: 0 auto;
  background-color: #00ffff;

  @media (min-width: @tablet-width) {
    height: 500px;
  }
}

.video-presentation__play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 27px 0 27px 48px;
  padding: 0;
  opacity: 0.7;
  transform: translate(-50%, -50%);
  background: inherit;
  border-color: transparent transparent transparent #ffffff;
  cursor: pointer;
}
